import * as React from "react";
import * as _ from "lodash";
import { withRouter, Redirect, Link } from "react-router-dom";
import { hot } from "react-hot-loader";
import { Button, Layout } from 'antd';
const { Content, Footer, Header } = Layout;
import { auth, firebase, firestore } from "../firebase";
import { useAuthState } from "react-firebase-hooks/auth";
import { useState, useContext } from 'react';
import { LoginUIContext } from "../providers/LoginUIProvider";

const Menu = () => {
  const [user, loading, error] = useAuthState(auth);
  const [isLoginModalVisible, setIsLoginModalVisible] = useContext(LoginUIContext);

  const loginClick = () => {
    setIsLoginModalVisible(true);
  }

  const logoutClick = () => {
    auth.signOut();
  }

  const tryUpdateProfile = (doc) => {
    if(!doc.exists) {
      firestore.collection('profiles').doc(user.uid).set({
        ownerId: user.uid,
        photoUrl: user.photoURL,
        displayName: user.displayName,
        created: new Date()
      });
    }
  };

  let loginLink = (<Button type="link" onClick={loginClick}>Login</Button>);

  if (!loading && user !== null) {
    firestore.collection('profiles').doc(user.uid).get().then(tryUpdateProfile);

    loginLink = (<><Link to={"/profile"}>Profile</Link><Button type="link" onClick={logoutClick}>Logout</Button></>);
  }

  let menu = (<><Link to={"/"}>Home</Link>&nbsp;<Link to={"/help"}>Help</Link>&nbsp;{loginLink}</>);
  if (loading) {
    menu = (<></>);
  }

  return (
    <>
      {menu}
    </>
  );
}

export default hot(module)(withRouter(Menu));
